.slider {
  display: flex;
}

.slider__item {
  padding: 20px 170px 20px 20px;
  background: $yellow-light;
  border-radius: 10px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @include desktop {
    padding: 0;
    padding-top: 18px;
    width: 280px;
    background: transparent;
  }
}

.slider__title {
  font-family: $museobold;
  font-size: 14px;
  line-height: 1.15;
  color: $white;

  @include desktop {
    font-size: 25px;
  }
}

.slider__percent {
  margin-top: 10px;
  font-family: $museoextrabold;
  font-size: 40px;
  line-height: 1.15;
  color: $white;

  @include desktop {
    margin-top: 20px;
    font-size: 80px;
  }
}

.slider__description {
  margin-top: 10px;
  font-family: $regular;
  font-size: 12px;
  line-height: 1.35;
  color: $white;

  @include desktop {
    margin-top: 45px;
    font-size: 15px;
  }
}

.slider__details {
  margin-top: 13px;
  padding: 10px 25px;
  font-family: $regular;
  font-size: 10px;
  color: $white;
  border: 1px solid $white;
  border-radius: 60px;

  @include desktop {
    margin-top: 45px;
    font-size: 14px;
    padding: 20px 45px;
  }
}
